import "./App.css";
import axios from "axios";
import { useEffect, useState } from "react";
function App() {
  const [noOfUsers, setnoOfUsers] = useState([]);

  useEffect(() => {
    axios
      .get("https://localhost:3001/users", {
        headers: {
          "Content-Type": "text/plain",
          "Access-Control-Allow-Origin": "*",
        },
      })
      .then((response) => {
        setnoOfUsers(response.data.data);
        console.log(response);
      });
  }, []);
  return (
    <div className="App">
      {noOfUsers.map((value, key) => {
        return (
          <div>
            <h1>{value.id}</h1>
            <h2>{value.email}</h2>
          </div>
        );
      })}
    </div>
  );
}

export default App;
